import { Injectable } from '@angular/core';
import { Keen } from 'keen-analysis';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Injectable()
export class KeenIoService {
  keen;
  constructor(
    private http: HttpClient
  ) {
    this.keen = new Keen(environment.keenio);
  }

  run (...args) {
    return this.keen.run(...args);
  }

  oldquery(...args) {
    return this.keen.query(...args);
  }

  query(analysisType: string, query: any) {
    const KeenAuthHeader: HttpHeaders = new HttpHeaders({
      Authorization: environment.keenio.readKey
    });
    const keenQuery = this.transformQuery(analysisType, query);

    return this.http.post(
      `https://api.keen.io/3.0/projects/${environment.keenio.projectId}/queries/${analysisType}`,
      keenQuery,
      {
        headers: KeenAuthHeader
      }
    ).pipe(
      map(result => {
        return {
          query: keenQuery,
          ...result
        };
      })
    ).toPromise();
  }

  private transformQuery(analysisType: string, query: any): any {
    const keenIoQuery = { ...query };

    keenIoQuery.analysis_type = analysisType;
    keenIoQuery.timezone = 'UTC';

    keenIoQuery.event_collection = query.eventCollection;
    delete keenIoQuery.eventCollection;

    if (query.groupBy) {
      keenIoQuery.group_by = query.groupBy;
      delete keenIoQuery.groupBy;
    }

    return keenIoQuery;
  }
}
