import { Injectable } from '@angular/core';
import { NgbCalendar, NgbDate } from '@ng-bootstrap/ng-bootstrap';
import { DatePickerRangeModel } from '@shared/components/date-picker-range/date-picker-range.model';

@Injectable()
export class UsersDateService {
  range: DatePickerRangeModel;

  constructor(calendar: NgbCalendar) {
    const today = calendar.getToday();

    this.range = {
      from: new NgbDate(2016, 1, 1),
      to: today,
    };
  }
}
