import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminGuard } from '@guards/admin.guard';
import { RedeemGuard } from '@guards/redeem.guard';

const routes: Routes = [
  {
    path: 'dashboard', loadChildren: () => import('./pages/dashboard/dashboard.module').then(m => m.DashboardModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'user/:userId/stats', loadChildren: () => import('./pages/user-stat/user-stat.module').then(m => m.UserStatModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'user/:userId', loadChildren: () => import('./pages/user-edit/user-edit.module').then(m => m.UserEditModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'users/create', loadChildren: () => import('./pages/user-create/user-create.module').then(m => m.UserCreateModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'users', loadChildren: () => import('./pages/users/users.module').then(m => m.UsersModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'tournaments', loadChildren: () => import('./pages/tournaments/tournaments.module').then(m => m.TournamentsModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'countries', loadChildren: () => import('./pages/countries/countries.module').then(m => m.CountriesModule),
    canActivate: [ AdminGuard ]
  }, {
    path: 'profile', loadChildren: () => import('./pages/profile/profile.module').then(m => m.ProfileModule),
    canActivate: [ RedeemGuard ]
  },
  { path: 'app-control', loadChildren: () => import('./pages/app-control/app-control.module').then(m => m.AppControlModule) }, {
    path: '**', pathMatch: 'full', redirectTo: '/admin/dashboard'
  }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
